<script setup>
import BaseHeader from '@/components/common/header/baseHeader.vue'
import BaseFooter from '@/components/common/footer/baseFooter.vue'
import { useStore } from "vuex";
import axios from "axios";
import { ref} from 'vue';

import { AgGridVue } from 'ag-grid-vue3'; // Vue3 AgGrid Component

import 'ag-grid-community/styles/ag-grid.css' // 그리드 기본 css
import 'ag-grid-community/styles/ag-theme-alpine.css' // 그리드 테마

  const store = useStore();
  const rowData = ref([]);

  const getBoardList = async () => {
    store.commit("changeStatus", true);
    await axios.post("/boardList.do",{})
    .then((res) => {
      console.log('조회결과',res.data.RESULT_LIST);
      rowData.value = res.data.RESULT_LIST;
      return res.data.RESULT_LIST;
    })
    .catch((res) => {
      console.error("에러", res);
      rowData.value = {};
      return 'ERROR'
    });
    store.commit("changeStatus", false);
  }

  const colDefs = ref([
    { field: "CATEGORY",headerName: '카테고리', checkboxSelection: true },
    { field: "CODE_NAME", headerName: '카테고리명',},
    { field: "TITLE" ,headerName: '제목',editable: true, },
    { field: "BODY_TEXT", headerName: '내용', },
    { field: "REG_DT2",headerName: '등록일',editable: true, cellEditor: 'agDateStringCellEditor',
        cellEditorParams: {
            min: '2020-01-01',
            max: '2027-12-31',
        } }
  ]);

  let gridObject = {};
  const onMountGrid =(prop) =>{
    console.log(prop);
    gridObject = prop.api;
  }

  const saveBoardData =() =>{

    gridObject.startEditingCell({
    rowIndex: 0,
    colKey: 'TITLE'
});

    console.log(gridObject.getSelectedNodes());
  }

</script>

<template>
<div>
  <BaseHeader title="게시판"></BaseHeader>
  <main>
    <div>
      게시판 리스트
    </div>
    <div>
      <button type="button" id="searchBtn" @click="getBoardList">조회</button>
      <button type="button" id="searchBtn" @click="saveBoardData">저장</button><br>
    </div>
    <div>
      <ag-grid-vue 
        style="height: 480px" 
        class="ag-theme-alpine" 
        :rowData="rowData" 
        :columnDefs="colDefs" 
        rowSelection="multiple"
        singleClickEdit = true 
        @grid-ready="onMountGrid"
      >
      </ag-grid-vue>
    </div>
  </main>
  <BaseFooter></BaseFooter>
</div>
</template>

